@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&display=swap");
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  transition: all 0.3ms;
}

body {
  font-family: "Open Sans";
  letter-spacing: normal;
}
.App {
  text-align: center;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.mb {
  display: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#popupCont {
  position: absolute;
  bottom: 300px;
  right: 300px;
  width: 25%;
  min-width: 350px;
}
.flex {
  display: flex;
}
.row {
  flex-direction: row;
}
.bold {
  font-weight: 800;
}
.highlight {
  color: #ff497a;
}
.mapboxgl-popup {
  .mapboxgl-popup-content {
    width: 25vw;
    background-color: transparent;
    padding: 0;
    .mapPopup {
      position: relative;
      background: #fff;
      border-radius: 3px;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
      pointer-events: auto;
      height: 150px;
      width: 100%;
      height: auto;
      text-align: left;
      color: #4a5584;
      cursor: default;
      h3 {
        background-color: #4a5584 !important;
        color: white;
        font-weight: 800;
        padding: 10px 10px 10px 40px;
      }
      p {
        font-size: 16px;
        margin: 6px 15px;
        &.severity {
          margin: 10px 0 0 15px;
        }
      }
      ul {
        margin: 8px 8px 0 25px;
        padding: 0 0 10px 0;
        font-size: 16px;
        list-style-image: url("./dot.svg");
        li {
          margin: 5px 0;
        }
      }
    }
  }
}

.weather-icon {
  width: 60px;
  height: 60px;
  padding: 4px;
  border-radius: 35px;
  background-color: white;
  border: 1px solid #d4dcfb;
  overflow: hidden;
  position: absolute;
  top: -10px;
  left: -25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.weather-icon img {
  width: 78%;
  height: auto;
}

#ui-cont {
  position: absolute;
  top: 0;
  width: 100%;
  user-select: none;
}
#map-title {
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: white;
  width: 50%;
  min-width: 640px;
  padding: 1.2rem 3rem;
  box-shadow: 0px 3px 6px #6b683729;
  position: absolute;
  top: 0;
  left: 25%;
  cursor: default;
  .title-texts {
    text-align: left;
    h2 {
      font-weight: 800;
      text-transform: uppercase;
    }
    h4 {
      font-weight: 600;
      text-transform: uppercase;
      color: #6b82eb;
    }
  }
  .accidents-text {
    p {
      font-size: 13px;
      font-weight: 600;
    }
    h1 {
      font-weight: 800;
      font-size: 40px;
      margin: -11px 0 0 0;
    }
    p,
    h1 {
      color: #ff497a;
    }
  }
}
.light-btn {
  border: 0.5px solid #d4dcfb;
  border-radius: 12px;
  min-height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 2px 2px rgba(94, 97, 112, 0.1);
  width: 90%;
  margin: 0.3rem 0;
  padding: 10px 0 5px 0;
  cursor: pointer;

  &.mobile {
    max-height: 65px;
    max-width: 80px;
    margin: 0 .5rem 0 0;
  }
  &:hover {
    &.active {
      box-shadow: 0px 2px 6px rgba(142, 160, 253, 0.7);
    }
    box-shadow: 0px 2px 6px rgba(142, 160, 253, 0.7);
  }
  img {
    height: 28px;
  }
  p {
    font-weight: 800;
    font-size: 13px;
    color: #b1b1b1;
    margin: 3px 0;
  }
  &.dark {
    border: 0.5px solid #7881ac;
    
    p {
      color: #7881ac !important;
    }
  }

  &.active {
    box-shadow: 0px 2px 2px rgba(142, 160, 253, 0.3);
    border: 0.5px solid #6b82eb;
    &.dark {
      background-color: #4a5583;
      border: 0.5px solid #717794;
      box-shadow: 0px 3px 6px #6f788570;
      p {
        color: white !important;
      }
    }
    p {
      color: #6b82eb;
    }
  }
}

.dark-sec {
  background-color: #2a3568;
  border-radius: 8px;
  padding: 3px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0.3rem 0 0 0;
  width: 100%;
  &.mobile {
    flex-direction: row;   
    padding: 0.5rem 0; 
    margin: 0.5rem 0 0 0;
    h4 {
      margin: 0 1rem 0 0;
    }  
  }
  h4 {
    color: white !important;
    font-size: 14px !important;
  }
  :nth-child(2) {
    p {
      font-size: 13px !important;
      text-transform: none !important;
    }
  }
  p {
    color: white !important;
  }
}
#mobile-light-menu {
  display: none;
  .title {
    font-weight: 800;
    font-size: 16px;
    color: #6b82eb;
    margin: 0.5rem 0;
    text-transform: uppercase;
  }
}
#light-menu {
  width: 110px;
  padding: 5px;
  background-color: white;
  margin: 0 0 0 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px 3px 6px #6b683729;
  position: absolute;
  bottom: 35px;
  left: 0.5rem;
  cursor: default;
  h4 {
    font-weight: 800;
    font-size: 13px;
    color: #6b82eb;
  }
  :nth-child(2) {
    p {
      font-size: 16px;
      text-transform: uppercase;
    }
  }
}

#mobile-light-menu {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0;
  background-color: white;
  width: 100%;
  padding: 0.5rem;
  box-shadow: 3px 0px 6px #6b683729;
  z-index: 2;
  display: none;
  .btns-cont {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
  }
}
#actual-hour{
  bottom: 70px !important;
}
#actual-light, #actual-hour {
  height: 70px;
  width: 70px;
  background-color: white;
  border-radius: 40px;
  position: absolute;
  bottom: 150px;
  right: 20px;
  box-shadow: 0px 3px 6px #6b683729;
  color: #6b82eb;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding: 5px;
  
  img {
    width: 40px;
    height: auto;
  }
}
@media (max-width: 1000px) {
  #map-title {
    left: 15%;
  }
}

@media (max-width: 800px) {
  .mapboxgl-popup .mapboxgl-popup-content {
    width: auto;
  }

  #mobile-light-menu {
    display: inherit;
  }

  #map-title {
    margin: 0;
    width: 100%;
    min-width: 100%;
    left: 0;
  }
}
@media (max-width: 640px) {
  #map-title {
    margin: 0;
    width: 100%;
    min-width: 100%;
    left: 0;
  }

  #light-menu {
    display: none;
  }
}

@media (max-width: 600px) {
  #map-title {
    padding: 1.5rem;
    .title-texts {
      h2 {
        font-size: 21px;
      }
    }
  }

  #light-menu {
    margin: 0 0 0 0.5rem;
  }
}

@media (max-width: 400px) {
  #map-title {
    margin: 0;
    padding: 1.5rem;
    .title-texts {
      h2 {
        font-size: 16px;
      }
      h4 {
        font-size: 14px;
      }
    }
  }
}
